import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import fileIcon from "../assets/images/documents.png"; // Your default icon

const MediaRendererFile = ({ url, onClick, className }) => {
  const [mediaType, setMediaType] = useState(null); // "image", "video", or "file"
  const [isIphone, setIsIphone] = useState(false);
  const [base64Image, setBase64Image] = useState(null);

  useEffect(() => {
    const userAgent = navigator.userAgent || "";
    if (/iPhone|iPad|iPod/i.test(userAgent)) {
      setIsIphone(true);
    }
  }, []);

  useEffect(() => {
    if (!url) return;

    const extension = url.split(".").pop().toLowerCase();
    const isAvif = extension === "avif";
    const imageTypes = ["jpg", "jpeg", "png", "gif", "webp", "avif"];
    const videoTypes = ["mp4", "webm", "ogg", "mov"];

    if (isIphone && isAvif) {
      fetch(url)
        .then((res) => res.blob())
        .then((blob) => {
          const reader = new FileReader();
          reader.onloadend = () => {
            setBase64Image(reader.result);
            setMediaType("image");
          };
          reader.onerror = () => setMediaType("file");
          reader.readAsDataURL(blob);
        })
        .catch(() => setMediaType("file"));
    } else if (imageTypes.includes(extension)) {
      setMediaType("image");
    } else if (videoTypes.includes(extension)) {
      setMediaType("video");
    } else {
      setMediaType("file");
    }
  }, [url, isIphone]);

  if (!mediaType) return <></>;

  return (
    <>
      {mediaType === "image" ? (
        isIphone && url.endsWith(".avif") ? (
          <img
            src={base64Image || ""}
            alt="media"
            className={className}
            style={{ maxWidth: "100%" }}
            onClick={onClick}
          />
        ) : (
          <picture>
            {url.endsWith(".avif") && <source srcSet={url} type="image/avif" />}
            <img
              src={url.endsWith(".avif") ? url.replace(".avif", ".jpg") : url}
              alt="media"
              className={className}
              style={{ maxWidth: "100%" }}
              onClick={onClick}
            />
          </picture>
        )
      ) : mediaType === "video" ? (
        <video
          controls
          style={{ maxWidth: "100%", cursor: "pointer" }}
          className={className}
          onClick={onClick}
        >
          <source src={url} />
        </video>
      ) : (
        <Link
          to={url}
          download
          target="_blank"
          title="File"
          rel="noopener noreferrer"
          className="h-100 d-inline-block chatfiles"
        >
          <img src={fileIcon} alt="File" style={{ maxWidth: "100%" }} />
        </Link>
      )}
    </>
  );
};

export default MediaRendererFile;
